.xxx-popup {
    position: fixed;
    right: 0;
    bottom: 0;
    border: 2px solid;
    max-width: 450px;
    z-index: 500;
    padding: 1rem 1rem 0.5rem;
    font-size: 16px;
    line-height: 1.5em;
    text-decoration: none;
    opacity: 0;
    -webkit-transition: 600ms ease;
    -moz-transition: 600ms ease;
    transition: 600ms ease;
    -webkit-transform: translateX(100%);
    -moz-transform: translateX(100%);
    transform: translateX(100%);
}
.xxx-popup--img {
    padding: 0;
    border: 0;
}
.xxx-popup--active {
    opacity: 1;
    -webkit-transform: translateX(0);
    -moz-transform: translateX(0);
    transform: translateX(0);
}
.xxx-popup h3 {
    font-weight: bold;
    font-size: 1.25em;
    line-height: 1.25em;
    margin: 0;
}
@media (max-width: 468px) {
    .xxx-popup {
        font-size: 13px;
    }
}
@media (max-width: 340px) {
    .xxx-popup {
        font-size: 12px;
    }
}
.xxx-popup a {
    text-decoration: none;
}
.xxx-popup img {
    display: block;
    max-width: 33.3%;
    height: auto;
    margin: 5px 10px 0.5rem;
}
.xxx-popup--img img {
    max-width: 100%;
    margin: 0;
}
.xxx-popup img.alignleft {
    float: left;
    margin-left: 0;
}
.xxx-popup img.alignright {
    float: right;
    margin-right: 0;
}
.xxx-popup img.aligncenter {
    margin-left: auto;
    margin-right: auto;
}
.xxx-popup__close {
    position: absolute;
    right: 0;
    bottom: 100%;
    height: 30px;
    line-height: 24px;
    border: 0;
    outline: 0;
    cursor: pointer;
    margin-bottom: 2px;
    color: #000;
    text-align: right;
    padding: 4px 34px 6px 10px;
    background: rgba(255, 255, 255, 0.9);
}
.xxx-popup--img .xxx-popup__close {
    margin-bottom: 0;
}
.xxx-popup__close::after,
.xxx-popup__close::before {
    content: '';
    position: absolute;
    right: 5px;
    top: 50%;
    width: 24px;
    height: 3px;
    margin-top: -1px;
    background: #000;
}
.xxx-popup__close::after {
    -webkit-transform: rotate(45deg);
    -moz-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    -o-transform: rotate(45deg);
    transform: rotate(45deg);
}
.xxx-popup__close::before {
    -webkit-transform: rotate(-45deg);
    -moz-transform: rotate(-45deg);
    -ms-transform: rotate(-45deg);
    -o-transform: rotate(-45deg);
    transform: rotate(-45deg);
}

.salon-card {
    -webkit-box-shadow: 0 1px 3px 0 rgba(0,0,0,0.25);
    box-shadow: 0 1px 3px 0 rgba(0,0,0,0.25);
    font-size: 0.875rem;
    line-height: 1.5em;
    margin: 15px 0;
}
.salon-card__left {
    padding: 1em;
}
.salon-card__gallery {
    position: relative;
    display: flex;
    overflow-x: auto;
    overflow-y: hidden;
    -webkit-overflow-scrolling: touch;
